describe("arrays", function(){

	it("doubles array", function() {
		let array = [1,2,3,4];

		let sum = 0;
		let doubled = array.map(function(n){
			return n*2;
		})

		let doubledes6 = array.map(n => n*2);

		expect(doubled).toEqual([2,4,6,8]);
		expect(doubledes6).toEqual([2,4,6,8]);

	});

});

describe("lexically bind this reference", function(){

	it("does not pass this", function(done) {

		this.name = "Laurel";
		setTimeout(function(){
			//because 'this' is now referring to this function
			expect(this.name).not.toBe("Laurel");
			done();
		}, 15)

	});

	it("we can use self", function(done) {

		var self = this;
		self.name = "Laurel";
		setTimeout(function(){
			//because 'this' is now referring to this function
			expect(self.name).toBe("Laurel");
			done();
		}, 15)

	});

	it("we can use bind", function(done) {

		this.name = "Laurel";
		setTimeout(function(){
			//because 'this' is now referring to this function
			expect(this.name).toBe("Laurel");
			done();
		}.bind(this), 15)

	});
	it("using arrow functions", function(done) {

		this.name = "Laurel";
		setTimeout(() => {
			//use the arrow to bind this
			expect(this.name).toBe("Laurel");
			done();
		}, 15)

	});

});

describe("for of", function(){

	it("loops", function() {
		let numbers = [1,2,3,4];

		let sum = 0;
		for(let n of numbers){
			sum += n;
		}

		expect(sum).toBe(10);

	});

});
